<template>
  <div id="main">
    <!-- Featured Post -->
      <article class="post featured">
        <header class="major">
          <span class="date" id="homeDate">{{ today }}</span>
          <h2>Bienvenue !</h2>
          <p>Les applications arriveront une à une au fur et à mesure de mes disponibilités...<br/>
            Ce portail est une application que vous pouvez installer sur votre téléphone,<br/>
            consulter à tout moment, même offline, et être tenu des avancées par notification.
          </p>
          <!-- div id="firebaseui-auth-container"></div -->
        </header>
      </article>

    <!-- Posts -->
      <section class="posts">
        <article>
          <header>
            <h2>Coming soon !</h2>
          </header>
          <a class="image fit"><img src="static/img/running.png" alt="" /></a>
          <p>En construction ...</p>
        </article>
        <article>
          <header>
            <h2>And next ...</h2>
          </header>
          <a class="image fit"><img src="static/img/travel.png" alt="" /></a>
          <p>D'autres projets à venir ...</p>
        </article>
      </section>
  </div>
</template>

<script>
export default {
  name: 'hello',

  data () {
    return {
      msg: 'Welcome to Your Vue.js PWA',
      today: ''
    }
  },

  mounted () {
    var today = new Date()
    var d = today.getDate()
    var m = this.getMonthStrings()[today.getMonth()]
    var y = today.getFullYear()
    this.today = d + ' ' + m + ' ' + y
  },

  methods: {
    getMonthStrings () {
      return ['janvier', 'février', 'mars', 'avril', 'mai', 'juin', 'juillet', 'août', 'septembre', 'octobre', 'novembre', 'décembre']
    }
  }
}
</script>

